import { VFC } from 'react';

import {
  Radio,
  RadioGroup,
  FormControlLabel,
  FormControl,
  FormLabel,
} from '@mui/material';

import { PublicRadioWrapper } from './InputElements';

const PublicSelect: VFC<{
  checkedNot: () => void;
  checkedPublic: () => void;
  checkedPrivate: () => void;
}> = ({ checkedNot, checkedPublic, checkedPrivate }) => (
  <PublicRadioWrapper>
    <FormControl component="fieldset">
      <FormLabel component="legend"></FormLabel>
      <RadioGroup
        row
        aria-label="public"
        defaultValue=""
        name="row-radio-buttons-group"
      >
        <FormControlLabel
          value=""
          control={<Radio size="small" />}
          label=""
          onChange={checkedNot}
        />
        <FormControlLabel
          value="true"
          control={<Radio size="small" />}
          label=""
          onChange={checkedPublic}
        />
        <FormControlLabel
          value="false"
          control={<Radio size="small" />}
          label=""
          onChange={checkedPrivate}
        />
      </RadioGroup>
    </FormControl>
  </PublicRadioWrapper>
);

export default PublicSelect;
